import { useCallback } from 'react'
import * as XLSX from 'xlsx'
import { toast } from 'sonner'

interface InvoiceItem {
  description: string
  quantity: number
  unitPrice: number
}

interface Client {
  id: string
  firstName: string
  lastName: string
  email: string
  phone: string
  address: string
}

interface Invoice {
  id: string
  invoiceNumber: string
  date: string
  dueDate: string
  items: InvoiceItem[]
  taxRate: number
  total: number
  client: Client
  status: string
}

export const useExcelExport = () => {
  const exportToExcel = useCallback((invoice: Invoice) => {
    try {
      // Calculate subtotal and tax
      const subtotal = invoice.items.reduce((sum, item) => sum + item.quantity * item.unitPrice, 0)
      const taxAmount = subtotal * (invoice.taxRate / 100)

      // Create workbook
      const workbook = XLSX.utils.book_new()

      // Invoice header data
      const headerData = [
        ['FACTURE'],
        [''],
        ['Numéro de facture:', invoice.invoiceNumber],
        ['Date d\'émission:', new Date(invoice.date).toLocaleDateString('fr-FR')],
        ['Date d\'échéance:', new Date(invoice.dueDate).toLocaleDateString('fr-FR')],
        ['Statut:', invoice.status === 'draft' ? 'Brouillon' : invoice.status === 'sent' ? 'Envoyée' : 'Payée'],
        [''],
        ['INFORMATIONS CLIENT'],
        ['Nom:', `${invoice.client.firstName} ${invoice.client.lastName}`],
        ['Email:', invoice.client.email],
        ['Téléphone:', invoice.client.phone],
        ['Adresse:', invoice.client.address],
        [''],
        ['DÉTAIL DES ARTICLES'],
        ['Description', 'Quantité', 'Prix unitaire (€)', 'Total (€)']
      ]

      // Add invoice items
      const itemsData = invoice.items.map(item => [
        item.description,
        item.quantity,
        item.unitPrice.toFixed(2),
        (item.quantity * item.unitPrice).toFixed(2)
      ])

      // Add totals
      const totalsData = [
        [''],
        ['', '', 'Sous-total:', subtotal.toFixed(2) + '€'],
        ['', '', `TVA (${invoice.taxRate}%):`, taxAmount.toFixed(2) + '€'],
        ['', '', 'TOTAL TTC:', invoice.total.toFixed(2) + '€']
      ]

      // Combine all data
      const allData = [
        ...headerData,
        ...itemsData,
        ...totalsData
      ]

      // Create worksheet
      const worksheet = XLSX.utils.aoa_to_sheet(allData)

      // Set column widths
      const columnWidths = [
        { wch: 30 }, // Description
        { wch: 10 }, // Quantity
        { wch: 15 }, // Unit Price
        { wch: 15 }  // Total
      ]
      worksheet['!cols'] = columnWidths

      // Style the header cells (make them bold)
      const headerCells = ['A1', 'A8', 'A14']
      headerCells.forEach(cell => {
        if (worksheet[cell]) {
          worksheet[cell].s = {
            font: { bold: true, sz: 14 },
            alignment: { horizontal: 'left' }
          }
        }
      })

      // Style the table header row
      const tableHeaderRow = 15 // Row 15 contains the table headers
      const tableHeaderCells = ['A15', 'B15', 'C15', 'D15']
      tableHeaderCells.forEach(cell => {
        if (worksheet[cell]) {
          worksheet[cell].s = {
            font: { bold: true },
            fill: { fgColor: { rgb: 'E9ECEF' } },
            alignment: { horizontal: 'center' }
          }
        }
      })

      // Style the totals section
      const totalsStartRow = headerData.length + itemsData.length
      for (let i = 0; i < totalsData.length; i++) {
        const row = totalsStartRow + i + 1
        const totalCell = `D${row}`
        if (worksheet[totalCell]) {
          worksheet[totalCell].s = {
            font: { bold: i === totalsData.length - 1 }, // Make the final total bold
            alignment: { horizontal: 'right' }
          }
        }
      }

      // Add worksheet to workbook
      XLSX.utils.book_append_sheet(workbook, worksheet, 'Facture')

      // Generate filename
      const filename = `facture-${invoice.invoiceNumber}-${new Date().toISOString().split('T')[0]}.xlsx`

      // Save file
      XLSX.writeFile(workbook, filename)

      toast.success('Facture exportée en Excel avec succès!')
    } catch (error) {
      console.error('Erreur lors de l\'export Excel:', error)
      toast.error('Erreur lors de l\'export Excel')
    }
  }, [])

  const exportClientsToExcel = useCallback((clients: Client[]) => {
    try {
      // Create workbook
      const workbook = XLSX.utils.book_new()

      // Header data
      const headerData = [
        ['LISTE DES CLIENTS'],
        [''],
        ['Prénom', 'Nom', 'Email', 'Téléphone', 'Adresse']
      ]

      // Client data
      const clientsData = clients.map(client => [
        client.firstName,
        client.lastName,
        client.email,
        client.phone,
        client.address
      ])

      // Combine data
      const allData = [...headerData, ...clientsData]

      // Create worksheet
      const worksheet = XLSX.utils.aoa_to_sheet(allData)

      // Set column widths
      const columnWidths = [
        { wch: 15 }, // First Name
        { wch: 15 }, // Last Name
        { wch: 25 }, // Email
        { wch: 15 }, // Phone
        { wch: 40 }  // Address
      ]
      worksheet['!cols'] = columnWidths

      // Style header
      if (worksheet['A1']) {
        worksheet['A1'].s = {
          font: { bold: true, sz: 14 },
          alignment: { horizontal: 'left' }
        }
      }

      // Style table headers
      const tableHeaderCells = ['A3', 'B3', 'C3', 'D3', 'E3']
      tableHeaderCells.forEach(cell => {
        if (worksheet[cell]) {
          worksheet[cell].s = {
            font: { bold: true },
            fill: { fgColor: { rgb: 'E9ECEF' } },
            alignment: { horizontal: 'center' }
          }
        }
      })

      // Add worksheet to workbook
      XLSX.utils.book_append_sheet(workbook, worksheet, 'Clients')

      // Generate filename
      const filename = `clients-${new Date().toISOString().split('T')[0]}.xlsx`

      // Save file
      XLSX.writeFile(workbook, filename)

      toast.success('Liste des clients exportée en Excel avec succès!')
    } catch (error) {
      console.error('Erreur lors de l\'export Excel des clients:', error)
      toast.error('Erreur lors de l\'export Excel des clients')
    }
  }, [])

  return {
    exportToExcel,
    exportClientsToExcel
  }
}
