import React from 'react'
import { Document, Page, Text, View, StyleSheet, Font } from '@react-pdf/renderer'

// Define styles for the PDF
const styles = StyleSheet.create({
  page: {
    flexDirection: 'column',
    backgroundColor: '#FFFFFF',
    padding: 30,
    fontSize: 12,
    fontFamily: 'Helvetica',
  },
  header: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginBottom: 30,
    borderBottom: 2,
    borderBottomColor: '#000000',
    paddingBottom: 10,
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    color: '#000000',
  },
  companyInfo: {
    textAlign: 'right',
  },
  clientInfo: {
    marginBottom: 30,
    padding: 15,
    backgroundColor: '#f8f9fa',
  },
  sectionTitle: {
    fontSize: 14,
    fontWeight: 'bold',
    marginBottom: 10,
    color: '#333333',
  },
  invoiceDetails: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginBottom: 30,
  },
  table: {
    marginBottom: 30,
  },
  tableHeader: {
    flexDirection: 'row',
    backgroundColor: '#e9ecef',
    padding: 8,
    fontWeight: 'bold',
  },
  tableRow: {
    flexDirection: 'row',
    borderBottom: 1,
    borderBottomColor: '#dee2e6',
    padding: 8,
  },
  col1: { width: '50%' },
  col2: { width: '15%', textAlign: 'center' },
  col3: { width: '20%', textAlign: 'right' },
  col4: { width: '15%', textAlign: 'right' },
  totals: {
    marginLeft: 'auto',
    width: '40%',
  },
  totalRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    padding: 5,
  },
  totalRowBold: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    padding: 5,
    fontWeight: 'bold',
    borderTop: 1,
    borderTopColor: '#000000',
    marginTop: 5,
  },
  footer: {
    position: 'absolute',
    bottom: 30,
    left: 30,
    right: 30,
    textAlign: 'center',
    fontSize: 10,
    color: '#666666',
  },
})

interface InvoiceItem {
  description: string
  quantity: number
  unitPrice: number
}

interface Client {
  id: string
  firstName: string
  lastName: string
  email: string
  phone: string
  address: string
}

interface Invoice {
  id: string
  invoiceNumber: string
  date: string
  dueDate: string
  items: InvoiceItem[]
  taxRate: number
  total: number
  client: Client
  status: string
}

interface PDFInvoiceProps {
  invoice: Invoice
}

const PDFInvoice: React.FC<PDFInvoiceProps> = ({ invoice }) => {
  const subtotal = invoice.items.reduce((sum, item) => sum + item.quantity * item.unitPrice, 0)
  const taxAmount = subtotal * (invoice.taxRate / 100)

  return (
    <Document>
      <Page size="A4" style={styles.page}>
        {/* Header */}
        <View style={styles.header}>
          <View>
            <Text style={styles.title}>FACTURE</Text>
            <Text>Numéro: {invoice.invoiceNumber}</Text>
          </View>
          <View style={styles.companyInfo}>
            <Text style={{ fontWeight: 'bold' }}>Votre Entreprise</Text>
            <Text>123 Rue de l'Entreprise</Text>
            <Text>75001 Paris, France</Text>
            <Text>Tél: 01 23 45 67 89</Text>
            <Text>Email: contact@entreprise.com</Text>
          </View>
        </View>

        {/* Client Information */}
        <View style={styles.clientInfo}>
          <Text style={styles.sectionTitle}>Facturé à:</Text>
          <Text>{invoice.client.firstName} {invoice.client.lastName}</Text>
          <Text>{invoice.client.address}</Text>
          <Text>Email: {invoice.client.email}</Text>
          <Text>Tél: {invoice.client.phone}</Text>
        </View>

        {/* Invoice Details */}
        <View style={styles.invoiceDetails}>
          <View>
            <Text style={styles.sectionTitle}>Détails de la facture</Text>
            <Text>Date d'émission: {new Date(invoice.date).toLocaleDateString('fr-FR')}</Text>
            <Text>Date d'échéance: {new Date(invoice.dueDate).toLocaleDateString('fr-FR')}</Text>
          </View>
        </View>

        {/* Items Table */}
        <View style={styles.table}>
          <View style={styles.tableHeader}>
            <Text style={styles.col1}>Description</Text>
            <Text style={styles.col2}>Quantité</Text>
            <Text style={styles.col3}>Prix unitaire</Text>
            <Text style={styles.col4}>Total</Text>
          </View>
          {invoice.items.map((item, index) => (
            <View key={index} style={styles.tableRow}>
              <Text style={styles.col1}>{item.description}</Text>
              <Text style={styles.col2}>{item.quantity}</Text>
              <Text style={styles.col3}>{item.unitPrice.toFixed(2)}€</Text>
              <Text style={styles.col4}>{(item.quantity * item.unitPrice).toFixed(2)}€</Text>
            </View>
          ))}
        </View>

        {/* Totals */}
        <View style={styles.totals}>
          <View style={styles.totalRow}>
            <Text>Sous-total:</Text>
            <Text>{subtotal.toFixed(2)}€</Text>
          </View>
          <View style={styles.totalRow}>
            <Text>TVA ({invoice.taxRate}%):</Text>
            <Text>{taxAmount.toFixed(2)}€</Text>
          </View>
          <View style={styles.totalRowBold}>
            <Text>Total TTC:</Text>
            <Text>{invoice.total.toFixed(2)}€</Text>
          </View>
        </View>

        {/* Footer */}
        <View style={styles.footer}>
          <Text>Merci pour votre confiance !</Text>
          <Text>Conditions de paiement: 30 jours net</Text>
        </View>
      </Page>
    </Document>
  )
}

export default PDFInvoice
