'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { toast } from 'sonner'
import { useForm, useFieldArray, Controller } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { PDFDownloadLink } from '@react-pdf/renderer'
import PDFInvoice from '@/components/PDFInvoice'
import { useExcelExport } from '@/hooks/useExcelExport'

const invoiceItemSchema = z.object({
  description: z.string().min(1, 'Description requise'),
  quantity: z.number().min(1, 'Quantité minimum 1'),
  unitPrice: z.number().min(0, 'Prix unitaire invalide'),
})

const invoiceSchema = z.object({
  clientId: z.string().min(1, 'Client requis'),
  invoiceNumber: z.string().min(1, 'Numéro de facture requis'),
  date: z.string().min(1, 'Date requise'),
  dueDate: z.string().min(1, 'Date d\'échéance requise'),
  items: z.array(invoiceItemSchema).min(1, 'Au moins un article requis'),
  taxRate: z.number().min(0).max(100, 'Taux de TVA invalide'),
})

type InvoiceFormData = z.infer<typeof invoiceSchema>

interface Client {
  id: string
  firstName: string
  lastName: string
  email: string
  phone: string
  address: string
}

interface Invoice extends InvoiceFormData {
  id: string
  status: 'draft' | 'sent' | 'paid'
  total: number
  client: Client
}

export default function InvoicesPage() {
  const [invoices, setInvoices] = useState<Invoice[]>([])
  const [clients, setClients] = useState<Client[]>([])
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [selectedInvoice, setSelectedInvoice] = useState<Invoice | null>(null)
  const { exportToExcel } = useExcelExport()

  const {
    register,
    handleSubmit,
    reset,
    control,
    watch,
    formState: { errors }
  } = useForm<InvoiceFormData>({
    resolver: zodResolver(invoiceSchema),
    defaultValues: {
      items: [{ description: '', quantity: 1, unitPrice: 0 }],
      taxRate: 20,
      date: new Date().toISOString().split('T')[0],
      dueDate: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
    }
  })

  const { fields, append, remove } = useFieldArray({
    control,
    name: 'items'
  })

  const watchedItems = watch('items')
  const watchedTaxRate = watch('taxRate')

  // Calculate totals
  const subtotal = watchedItems?.reduce((sum, item) => {
    return sum + (item.quantity || 0) * (item.unitPrice || 0)
  }, 0) || 0

  const taxAmount = subtotal * (watchedTaxRate || 0) / 100
  const total = subtotal + taxAmount

  useEffect(() => {
    loadInvoices()
    loadClients()
  }, [])

  const loadInvoices = async () => {
    try {
      const response = await fetch('/api/invoices')
      if (response.ok) {
        const data = await response.json()
        setInvoices(data)
      }
    } catch (error) {
      console.error('Erreur lors du chargement des factures:', error)
    }
  }

  const loadClients = async () => {
    try {
      const response = await fetch('/api/clients')
      if (response.ok) {
        const data = await response.json()
        setClients(data)
      }
    } catch (error) {
      console.error('Erreur lors du chargement des clients:', error)
    }
  }

  const onSubmit = async (data: InvoiceFormData) => {
    setIsLoading(true)
    try {
      const response = await fetch('/api/invoices', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...data,
          total: subtotal + (subtotal * data.taxRate / 100)
        }),
      })

      if (response.ok) {
        const newInvoice = await response.json()
        setInvoices([...invoices, newInvoice])
        toast.success('Facture créée avec succès!')
        reset()
        setIsDialogOpen(false)
      } else {
        toast.error('Erreur lors de la création de la facture')
      }
    } catch (error) {
      toast.error('Erreur lors de la création de la facture')
      console.error('Erreur:', error)
    } finally {
      setIsLoading(false)
    }
  }

  const handleExcelExport = (invoice: Invoice) => {
    exportToExcel(invoice)
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'draft':
        return <Badge variant="secondary">Brouillon</Badge>
      case 'sent':
        return <Badge variant="default">Envoyée</Badge>
      case 'paid':
        return <Badge variant="outline" className="border-green-500 text-green-700">Payée</Badge>
      default:
        return <Badge variant="secondary">{status}</Badge>
    }
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Gestion des Factures</h1>
          <p className="text-gray-600">Créez et gérez vos factures</p>
        </div>
        
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button>Créer une facture</Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[800px] max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>Créer une nouvelle facture</DialogTitle>
              <DialogDescription>
                Remplissez les détails de la facture ci-dessous.
              </DialogDescription>
            </DialogHeader>
            <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="clientId">Client</Label>
                  <Controller
                    name="clientId"
                    control={control}
                    render={({ field }) => (
                      <Select onValueChange={field.onChange} value={field.value}>
                        <SelectTrigger>
                          <SelectValue placeholder="Sélectionner un client" />
                        </SelectTrigger>
                        <SelectContent>
                          {clients.map((client) => (
                            <SelectItem key={client.id} value={client.id}>
                              {client.firstName} {client.lastName}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    )}
                  />
                  {errors.clientId && (
                    <p className="text-sm text-red-500">{errors.clientId.message}</p>
                  )}
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="invoiceNumber">Numéro de facture</Label>
                  <Input
                    id="invoiceNumber"
                    {...register('invoiceNumber')}
                    placeholder="FAC-001"
                  />
                  {errors.invoiceNumber && (
                    <p className="text-sm text-red-500">{errors.invoiceNumber.message}</p>
                  )}
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="date">Date d'émission</Label>
                  <Input
                    id="date"
                    type="date"
                    {...register('date')}
                  />
                  {errors.date && (
                    <p className="text-sm text-red-500">{errors.date.message}</p>
                  )}
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="dueDate">Date d'échéance</Label>
                  <Input
                    id="dueDate"
                    type="date"
                    {...register('dueDate')}
                  />
                  {errors.dueDate && (
                    <p className="text-sm text-red-500">{errors.dueDate.message}</p>
                  )}
                </div>
              </div>

              <div className="space-y-4">
                <div className="flex justify-between items-center">
                  <Label>Articles</Label>
                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={() => append({ description: '', quantity: 1, unitPrice: 0 })}
                  >
                    Ajouter un article
                  </Button>
                </div>
                
                {fields.map((field, index) => (
                  <div key={field.id} className="grid grid-cols-12 gap-2 items-end">
                    <div className="col-span-5">
                      <Label htmlFor={`items.${index}.description`}>Description</Label>
                      <Input
                        {...register(`items.${index}.description`)}
                        placeholder="Description de l'article"
                      />
                    </div>
                    <div className="col-span-2">
                      <Label htmlFor={`items.${index}.quantity`}>Quantité</Label>
                      <Input
                        type="number"
                        {...register(`items.${index}.quantity`, { valueAsNumber: true })}
                        min="1"
                      />
                    </div>
                    <div className="col-span-3">
                      <Label htmlFor={`items.${index}.unitPrice`}>Prix unitaire</Label>
                      <Input
                        type="number"
                        step="0.01"
                        {...register(`items.${index}.unitPrice`, { valueAsNumber: true })}
                        min="0"
                      />
                    </div>
                    <div className="col-span-2">
                      {fields.length > 1 && (
                        <Button
                          type="button"
                          variant="outline"
                          size="sm"
                          onClick={() => remove(index)}
                        >
                          Supprimer
                        </Button>
                      )}
                    </div>
                  </div>
                ))}
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="taxRate">Taux de TVA (%)</Label>
                  <Input
                    id="taxRate"
                    type="number"
                    step="0.01"
                    {...register('taxRate', { valueAsNumber: true })}
                    min="0"
                    max="100"
                  />
                  {errors.taxRate && (
                    <p className="text-sm text-red-500">{errors.taxRate.message}</p>
                  )}
                </div>
              </div>

              <div className="bg-gray-50 p-4 rounded-lg">
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span>Sous-total:</span>
                    <span>{subtotal.toFixed(2)}€</span>
                  </div>
                  <div className="flex justify-between">
                    <span>TVA ({watchedTaxRate}%):</span>
                    <span>{taxAmount.toFixed(2)}€</span>
                  </div>
                  <div className="flex justify-between font-bold text-lg border-t pt-2">
                    <span>Total:</span>
                    <span>{total.toFixed(2)}€</span>
                  </div>
                </div>
              </div>
              
              <div className="flex justify-end space-x-2">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => setIsDialogOpen(false)}
                >
                  Annuler
                </Button>
                <Button type="submit" disabled={isLoading}>
                  {isLoading ? 'Création...' : 'Créer la facture'}
                </Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Liste des factures</CardTitle>
          <CardDescription>
            {invoices.length} facture{invoices.length !== 1 ? 's' : ''} créée{invoices.length !== 1 ? 's' : ''}
          </CardDescription>
        </CardHeader>
        <CardContent>
          {invoices.length === 0 ? (
            <div className="text-center py-8">
              <p className="text-gray-500 mb-4">Aucune facture créée</p>
              <p className="text-sm text-gray-400">
                Commencez par créer votre première facture
              </p>
            </div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Numéro</TableHead>
                  <TableHead>Client</TableHead>
                  <TableHead>Date</TableHead>
                  <TableHead>Échéance</TableHead>
                  <TableHead>Montant</TableHead>
                  <TableHead>Statut</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {invoices.map((invoice) => (
                  <TableRow key={invoice.id}>
                    <TableCell className="font-medium">{invoice.invoiceNumber}</TableCell>
                    <TableCell>
                      {invoice.client ? `${invoice.client.firstName} ${invoice.client.lastName}` : 'Client inconnu'}
                    </TableCell>
                    <TableCell>{new Date(invoice.date).toLocaleDateString('fr-FR')}</TableCell>
                    <TableCell>{new Date(invoice.dueDate).toLocaleDateString('fr-FR')}</TableCell>
                    <TableCell>{invoice.total.toFixed(2)}€</TableCell>
                    <TableCell>{getStatusBadge(invoice.status)}</TableCell>
                    <TableCell>
                      <div className="flex space-x-2">
                        <PDFDownloadLink
                          document={<PDFInvoice invoice={invoice} />}
                          fileName={`facture-${invoice.invoiceNumber}.pdf`}
                        >
                          {({ loading }) => (
                            <Button variant="outline" size="sm" disabled={loading}>
                              {loading ? 'Génération...' : 'PDF'}
                            </Button>
                          )}
                        </PDFDownloadLink>
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handleExcelExport(invoice)}
                        >
                          Excel
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
